<template>
    <div>
        <div class="search">
            <input v-model="keyword" type="text" placeholder="请输入城市名">
        </div>
        <div class="content" v-show="keyword">
            <ul>
                <li class="item" v-for="(item,index) in list" :key="index" @click="handleChangeCity(item.name)">
                    {{item.name}}
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import {mapMutations} from 'vuex'
export default {
    name:"CitySearch",
    data(){
        return{
            keyword:"",
            list:[],
            time:null
        }
    },
    methods:{
        handleChangeCity(city){
            this.changeCity(city)
            this.$router.push('/')
        },
        ...mapMutations(['changeCity'])
    },
    props:{
        cities:Object
    },
    watch:{
        keyword(){
            var arr = []
            if(this.time){
                clearTimeout(this.time)
            }
            this.time = setTimeout(()=>{
                for(let key in this.cities){
                    this.cities[key].forEach(element => {
                        // console.log(element)id: (...),name: "依兰",spell: "yilan"
                        if(element.name.indexOf(this.keyword) > -1 || element.spell.indexOf(this.keyword)>-1){
                            arr.push(element)
                        }
                    });
                }
                this.list = arr
            },500)
            
        }
    }
}
</script>
<style scoped>
    .search{
        height: 30px;
        background-color: turquoise;
        line-height: 30px;
        text-align: center;
    }
    .search input{
        text-align: center;
        border-radius: 5px;
        border: 1px solid #eee;
        width: 95%;
    }
    .content{
        position: absolute;
        background-color: #ccc;
        top: 60px;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .item{
        color: #666;
        background-color: #fff;
        padding-left: 10px;
        line-height: 30px;
    }
</style>


