<template>
    <div class="header">
        <div class="left">
            <span class="iconfont">&#xe601;</span>
        </div>
        <div class="search">
            <span class="iconfont">&#xe628;</span>
            <span class="zi">请输入城市</span>
        </div>
        <div class="right">
            <router-link to="/city">
                {{city}}
                <span class="iconfont">&#xe60f;</span>
            </router-link>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
    name:"HomeHeader",
    computed:{
        ...mapState(['city'])
    }
}
</script>
<style scoped>
.header{
    display: flex;
    justify-content: space-between;
    height: 30px;
    background-color: #00bcd4;
    line-height: 30px;
    color: #fff;
}
.search{
    flex: 1;
    background-color: #fff;
    color: #999;
    border-radius: .133333rem;
    margin: .133333rem;
    line-height: 25px;
    padding-left: 5px;
}
.right a{
    color: #fff;
}
</style>


