<template>
    <div class="alphabet">
        <ul>
            <li v-for="(item,index) in cities" :key="index" 
            @click="handleLetter"
            @touchstart.prevent="handleTouchStart"
            @touchmove="handleTouchMove"
            @touchend="handleTouchEnd"
            >{{index}}</li>
            
        </ul>
    </div>
</template>
<script>
export default {
    name:"CityAlphabet",
    data(){
        return{
            moveStatus:false
        }
    },
    computed:{
        //声明一个城市列表的数组
        letters(){
            const letters = []
            for(let i in this.cities){
                letters.push(i)
            }
            return letters
        }
    },
    props:{
        cities:Object
    },
    methods:{
        handleLetter(e){
            console.log(e.target)
            var letter = e.target.innerText
            this.$emit("letterHandle",letter)
        },
        // 手指按下
        handleTouchStart(){
            this.moveStatus = true
        },
        // 移动
        handleTouchMove(e){
            // console.log(e.touches[0].clientY)
            
            if(this.moveStatus){
                var touchY = e.touches[0].clientY - 137
                var index = Math.floor(touchY/17.3)
                console.log(index)

                if(index>=0&&index<this.letters.length){
                    var letter = this.letters[index]
                    this.$emit("letterHandle",letter)
                }
            }
            
        },
        handleTouchEnd(){
            this.moveStatus = false
        }
    }
}
</script>
<style scoped>
    ul{
        position: absolute;
        right: 0;
        bottom: 50px;
    }
    li{
        margin: 10px 0 ;
    }
</style>


