<template>
    <div class="lconlist">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(item,index) in pages" :key="index">
                <div class="icon" v-for="(it,ind) in item" :key="ind">
                    <div class="lcon-img">
                        <img :src="it.imgUrl" alt="">
                    </div>
                    <div class="lcon-con">
                        {{it.desc}}
                    </div>
                </div>
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>
<script>
export default {
    name:"HomeLconList",
    props:{
        list:Array
    },
    computed:{
        pages(){
            let pages = []
            this.list.forEach((item,index)=>{
                var page = Math.floor(index/8)
                if(!pages[page]){
                    pages[page] = []
                }
                pages[page].push(item)
            })
            return pages
        }
    }
}
</script>
<style scoped>
    .lconlist{
        height: 200px;
    }
    .icon{
        width: 25%;
        float: left;
        text-align: center;
        padding: 5px 0;
    }
    .icon .lcon-img img{
        width: 70%;
    }
</style>


