<template>
    <div class="list" ref="wrapper">
        <div class="box">
            <div class="nowcity">
                <h2>当前城市</h2>
                <div class="content">
                    <div class="button">
                        {{city}}
                    </div>
                </div>
            </div>
            <div class="hotcity">
                <h2>热门城市</h2>
                <div class="content">
                    <div class="button" @click="handleChangeCity(item.name)" v-for="(item,index) in hotCities" :key="index" >
                        {{item.name}}
                    </div>
                    
                </div>
            </div>
            <div class="citylist" v-for="(item,index) in cities"  :key="index" :ref="index">
                <div >
                    <h2>{{index}}</h2>
                    <ul>
                        <li v-for="(it,ind) in item" :key="ind"  @click="handleChangeCity(it.name)">{{it.name}}</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
import {mapState,mapMutations} from 'vuex'
export default {
    name:"CityList",
    computed:{
        ...mapState(['city'])
    },
    props:{
        cities:Object,
        hotCities:Array,
        letter:String
    },
    methods:{
        handleChangeCity(city){
            this.changeCity(city)
            this.$router.push("/")
        },
        ...mapMutations(['changeCity'])
    },
    mounted(){
        let wrapper = this.$refs.wrapper
        this.scroll = new BScroll(wrapper)
    },
    watch:{
        letter(){
            this.scroll.scrollToElement(this.$refs[this.letter][0])
        }
    }
}
</script>
<style scoped>
    .box{
        position: absolute;
        left: 0;
        right: 0;
        overflow: hidden;
        z-index: -999;

    }
    .list h2{
        height: 25px;
        background-color: lightgrey;
        line-height: 25px;
    }
    .button{
        border: 1px solid #eee;
        float: left;
        height: 20px;
        width: 60px;
        line-height: 20px;
        text-align: center;
        border-radius :5px;
        width: 30%;
        margin: 5px ;
    }
    .content{
        height: 70px;
        background-color: #fff;
    }
    .citylist li{
        margin: 5px 0 10px 5px;
    }
</style>


