<template>
    <div class="recommend">
        <h2>热销推荐</h2>
        <div class="recom" v-for="(item,index) in list" :key="index">
            <div class="left">
                <img :src="item.imgUrl" alt="">
            </div>
            <div class="right">
                <p class="ryi">{{item.title}}</p>
                <p class="rer">{{item.desc}}</p>
                <a href="" class="rsan">查看详细</a>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"HomeRecommend",
    props:{
        list:Array
    }
    
}
</script>
<style scoped>
    .recom{
        height: 100px;
        display: flex;
        padding: 5px;
    }
    .left{
        float: left;
        width: 100px;
    }
    .left img{
        width: 100%;
    }
    .right{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        float: left;
        padding-left: 10px;
    }
    .ryi{
        font-size: 15px;
    }
    .rer{
        color: #999;
    }
    .rsan{
        display: inline-block;
        height: 20px;
        width: 80px;
        background-color: orange;
        border-radius: 5px;
        color: #fff;
        text-align: center;
        line-height: 20px;
    }
</style>


