<template>
    <div class="weekend">
        <h2>周末去哪</h2>
        <div class="week" v-for="(item,index) in list" :key="index">
            <img :src="item.imgUrl" alt="">
            <p>{{item.title}}</p>
            <p>{{item.desc}}</p>
        </div>
    </div>
</template>
<script>
export default {
    name:"HomeWeekend",
    props:{
        list:Array
    }
}
</script>
<style scoped>

    .weekend h2{
        line-height: 40px;
        background-color: #eee;
    }
    .week{
        height: 200px;
    }
    .week img{
        width: 100%;
    }
</style>


